import React, {useState} from "react";
import { Link } from "react-router-dom"
import styled from 'styled-components';
import img from '../../images/examplepic01.png';
import { WiNightCloudyHigh } from "react-icons/wi"
import { Button,Modal, ModalHeader, ModalBody, ModalFooter,Container, Row } from 'reactstrap';

const ScholarDetail = ({ scholar, loading, user, onRemove, onEdit })=>{
  const [modal, setModal]=useState(false);
  const toggle=()=>setModal(!modal);
  const ScholarStyle={
      borderBottom: "1px solid #444444",
      paddingTop: '50px',
      paddingBottom:'60px',
      height: '50px',
  }
  const titleSubStyle={
      fontSize: '12px',
      weight: '750px',
      marginLeft: '20px',
      marginTop:'5px',
      textAlign: 'right'
  }
  const titleStyle={
    fontSize: '20px',
    fontWeight: 'bold',
    weight: '750px',
    marginLeft:'auto',
    marginBottom:'10px',
    marginTop:'5px',
    textAlign:'left'
}
  const bodyStyle = {
    margin:'10px',
    textAlign:'left'
  }
  const cardStyle = {
      width: '83%',
      marginLeft: 'auto',
      marginRight: 'auto',
      paddingBottom:'50px'
  }
  const ScholarTextStyle = {
      fontSize: '30px',
      paddingBottom:'30px'
  }

  const imgStyle = {
    margin:'20px',
    textAlign: 'center'
  }


if(loading || !scholar){
  return null;
}

const { userId, id, title, body } = scholar;


const usertype = (user.id===1)


return(
  <div>
    <meta name="viewport" content="width=device-width, initial-scale=1.0" />
    <Container style={cardStyle}>
      <Row style={ScholarStyle}>
        <div  style={{fontSize:'30px', textAlign: 'left'}}><WiNightCloudyHigh style={{marginBottom:'10px'}}/>  </div>
      </Row>
      <div style={titleStyle}>{title}</div>
      <div style={titleSubStyle}>: {userId}</div>
      <div style={titleSubStyle}>: .hwp</div>
      <hr size="3" noshade></hr>
      <Row style={bodyStyle}>
        {body}
        </Row>
        <div style={{textAlign: 'center'}}>
        <img src={img}  height='auto' width='80%' style={imgStyle}/></div>
      <hr size="5" noshade></hr>
      <div style={{textAlign: 'center'}}>
        {
          usertype &&
          <Button color="primary" onClick={toggle}></Button>
        }
      <Modal isOpen={modal} toggle={toggle}>
        <ModalHeader toggle={toggle}>{title}</ModalHeader>
<ModalBody>1.  :   ,  ,  ,  ,    ,       
<br/>
2.  : (), , , , <br/>
3.        ,        .<br/>
4.     ,  , (/ ) .      .
</ModalBody>
<ModalFooter>
 <Link to='/applies'><Button color="primary" onClick={toggle}></Button></Link>
  <Button color="secondary" onClick={toggle}></Button>
</ModalFooter>
      </Modal>
     &nbsp; &nbsp;
     {
       usertype? 
       <Link to='/scholars'><Button color="secondary"></Button></Link>
       :<Link to='/scholarships'><Button color="secondary"></Button></Link>
     }
        
      </div>
      <div style={{textAlign: 'right'}}>
        {
          !usertype &&
          <div>
          <Button outline color="primary" onClick={onEdit}></Button>&nbsp;&nbsp;
          <Button outline color="danger" onClick={onRemove}></Button>
          </div>
        }
      </div>
    </Container>
  </div>
  );

}

export default ScholarDetail;

const Appcontainer = styled.div`
    &,
    & * {
        box-sizing: border-box;
    }
`;