import React from 'react'
import { Table, Badge, Container, Row, Col, Button } from 'reactstrap';
import { Link } from "react-router-dom"


const StudentDetail = ({ student, loading, scholarId }) =>{
    if(loading || !student){
        return null;
      }

    const titleStyle={
        fontSize:'25px',
        fontWeight:'bold',
        margin:'5px',
    }

    const subTitleStyle={
        fontSize:'20px',
        margin:'5px',
    }

    return (
        <Container style={{margin:'20px auto'}}>
            <Row style={titleStyle}><Badge color="info"> </Badge></Row>
            <Row>
            <Table borderless size="sm">
                    <thead>
                        <tr>
                            <th> {student.id}</th>
                            <th>{student.name} </th>
                            <th>{student.city} </th>
                            <th>{student.zipcode}/4.3</th>
                            <th>{student.phone}</th>
                        </tr>
                    </thead>
                </Table>
            </Row>
            <br/><br/>
            <Row style={titleStyle}><Badge color="info">   </Badge></Row>
            <Row>
                <Table size="sm">
                    <thead>
                        <tr>
                            <th> </th>
                            <th></th>
                            <th></th>
                            <th> </th>
                            <th> ()</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <th scope="row">1</th>
                            <td>A</td>
                            <td> </td>
                            <td>2019-09-18</td>
                            <td>300,000</td>
                        </tr>
                        <tr>
                            <th scope="row">2</th>
                            <td>B</td>
                            <td> </td>
                            <td>2019-10-28</td>
                            <td>2,000,000</td>
                        </tr>
                        <tr>
                            <th scope="row">3</th>
                            <td>C</td>
                            <td></td>
                            <td>-</td>
                            <td>-</td>
                        </tr>
                        <tr>
                            <th scope="row">4</th>
                            <td>D</td>
                            <td></td>
                            <td>-</td>
                            <td>-</td>
                        </tr>
                    </tbody>
                </Table>
            </Row>
            <br/>
            <Link to={`/selections/${scholarId}`}><Button></Button></Link>
        </Container>
    )
}

export default StudentDetail;